.round-change-container {
	text-align: center;
	width: 100%;
}

.round-change-button {
	background: #021275;
	border: 2px solid #ddd;
	border-radius: 0;
	color: #fff;
	font-family: 'Spectral', serif;
	font-size: 1.2rem;
	line-height: 2rem;
	padding: .6rem 1rem .4rem;
	text-transform: uppercase;
	text-shadow: 2px 2px #000;
	width: 100%;
}

.round-change-button:hover {
	color: #f0b96c;
	cursor: pointer;
}



