.daily-double {
	background: #021275;
	background-image: none;
	color: #eee;
	font-size: 1.6rem;
	font-family: 'Spectral', serif;
	font-weight: 500;
	height: 3rem;
	letter-spacing: 0;
	margin-bottom: 2px;
	text-transform: uppercase;
	text-shadow: 2px 2px #000;
	-webkit-transition: all 0.6s ease;
	-o-transition: all 0.6s ease;
	transition: all 0.6s ease;
	width: 100%;
}

.daily-double:hover {
	background-image: -webkit-gradient(linear, left top, left bottom, from(#8c0233), to(#3e01a3));
	background-image: -webkit-linear-gradient(top, #8c0233, #3e01a3);
	background-image: -o-linear-gradient(top, #8c0233, #3e01a3);
	background-image: linear-gradient(to bottom, #8c0233, #3e01a3);
	color: #eee;
}



